export default function sketch(p){
    let canvas;
    let rot;
    let a, b, c;
    let cx, cy;
    p.setup = () => {
      canvas = p.createCanvas(500, 500);
      p.background(240);
      a = p.createVector(0, -50);
      b = p.createVector(50, 50);
      c = p.createVector(-50, 50)

      cx = (a.x + b.x + c.x) / 3;
      cy = (a.y + b.y + c.y) / 3;

      rot = 0;
    }

    p.draw = () => {
      p.frameRate(60);
      p.background(240);
      p.translate(p.width/2, p.height/2);

      rot = p.degrees(p.atan2(p.mouseY - (p.height/2 + cy), p.mouseX - (p.width/2 + cx)));
      p.push();
      p.rotate(p.radians(rot));
      p.fill(255, 0, 0);
      p.triangle(a.x, a.y, b.x, b.y, c.x, c.y);
      p.pop();
    }
}