export default function sketch(p){
    let canvas;
    let r, xoff, yoff;
    let px, py, pa;

    p.setup = () => {
      canvas = p.createCanvas(500, 500);
      p.background(255);
      r = 100;
      xoff = p.width/2;
      yoff = p.height/2
      px = xoff;
      py = yoff + r;
      pa = 0;
    }

    p.draw = () => {
      p.frameRate(60);
      p.background(240)
      for(let a = 0; a < 361; a++){
        let x = r * p.sin(p.radians(a)) + xoff;
        let y = r * p.cos(p.radians(a)) + yoff;
        p.stroke(0);
        p.strokeWeight(3)
        p.point(x, y);
      }
      
      if(p.keyIsPressed && p.keyCode == 37){
        pa++;
        px = r * p.sin(p.radians(pa)) + xoff;
        py = r * p.cos(p.radians(pa)) + yoff;
      }

      if(p.keyIsPressed && p.keyCode == 39){
        pa--;
        px = r * p.sin(p.radians(pa)) + xoff;
        py = r * p.cos(p.radians(pa)) + yoff;
      }

      p.stroke(255, 0, 0);
      p.strokeWeight(10);
      p.point(px, py);
    }
}