export default function sketch(p){
    let canvas;
    let acc, vel, pos, amp, a, xoff;

    p.setup = () => {
      canvas = p.createCanvas(500, 500);
      p.background(240);
      xoff = p.width/2
      acc = p.createVector(0, 0.1);
      vel = p.createVector(0, 0)
      pos = p.createVector(xoff, 50);
      amp = 2;
      a = 0;
    }

    p.draw = () => {
      p.frameRate(60);
      p.background(240)
      p.fill(255, 0, 0);
      
      p.ellipse(pos.x, pos.y, 50, 50);

      a++;
      acc.x = amp * p.cos(p.radians(a));
      vel.add(acc);
      pos.add(vel);
      vel = p.createVector(0, 0);  
      if(pos.y >= p.height) pos.y = 0;
    }
}