import React from 'react'
import sketch from '../sketches/move_wave'
import P5Wrapper from 'react-p5-wrapper';
import prism from 'prismjs'

class SlideThree extends React.Component{

    constructor(){
        super();
        this.styleCenter = {
            display: 'flex', alignContent: 'center', justifyContent: 'center',
            marginBottom: '50px'
        }

        this.styleSideMargin ={
            marginLeft: '100px',
            marginRight: '100px'
        }

        this.styleParagraph = {
            fontSize: '20px',
            display: 'flex', alignContent: 'center', justifyContent: 'center',
            marginLeft: '100px',
            marginRight: '100px'
        }
        this.styleFooter = {
            fontSize: '15px',
            display: 'flex', alignContent: 'center', justifyContent: 'center'
        }
    }

    componentDidMount(){
        prism.highlightAll();
    }

    render(){
        return(
            <div>
                <h3 style={{display: 'flex', alignContent: 'center', justifyContent: 'center'}}>
                    Ruch na wzor fali
                </h3>

                <div style={{display: 'flex', alignContent: 'center', justifyContent: 'center'}}>
                    <P5Wrapper sketch={sketch}></P5Wrapper>
                </div>

                <p style={this.styleParagraph}>
                   Innym ciekawym zastosowaniem funkcji trygonometrycznych jest mapowanie do nich przyspieszenia
                   lub pozycji obiektow. Powyższa animacja przedstawia obiekt którego wektor przyspieszenia zostal 
                   zmapowany do funkcji cosinus kata a: (0, 360).
                </p>

                <pre style={this.styleSideMargin}><code class="language-javascript">
                {
                `
                    function draw(){
                        acc.y = 1;
                        acc.x = amp * p.cos(angle);
                        vel.add(acc);
                        pos.add(vel);

                        p.ellipse(pos.x, pos.y, 50, 50);
                    }
                  
                `
                }
                </code></pre>     

                <p style={this.styleParagraph}>
                    W powyższym przykładzie acc, vel i pos to wektory odpowiednio przyśpieszenia, prędkości oraz
                    pozycji. Współrzędna x wektora przyśpieszenia została zmapowana do funkcji cosinus.
                </p><br></br><br></br>

                <p style={this.styleFooter}>
                    Przygotowane w celach edukacyjnych. &reg;Michał Szóstak 2019r
                </p>               
            </div>    
        );
    }
}

export default SlideThree;