export default function sketch(p){
    let canvas;
    let x, y, r, a, xoff, yoff;
    let resetButton;

    function reset() {
      p.clear();
      p.setup();
    }

    p.setup = () => {
      canvas = p.createCanvas(500, 500);
      p.background(240);
      x = p.width / 2;
      y = p.height / 2;
      r = 100;
      a = 0;
      xoff = p.width/2;
      yoff = p.height/2
    }

    p.draw = () => {
      p.frameRate(60);
      a += 1;
      x = r * p.sin(p.radians(a)) + xoff;
      y = r * p.cos(p.radians(a)) + yoff;
      p.stroke(0);
      p.strokeWeight(3)
      p.point(x, y);

      if(p.keyIsPressed && p.keyCode == 32){
        reset();
      }
    }
}