import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import { Route, Link, BrowserRouter} from 'react-router-dom'
import {hashHistory} from 'react-router'
import * as serviceWorker from './serviceWorker';
import SlideOne from './components/slide-one';
import SlideTwo from './components/slide-two';
import SlideThree from './components/slide-three';
import SlideFour from './components/slide-four';
import SlideFive from './components/slide-five';

const routing = (
    <BrowserRouter>
        <Route path="/" component={App} />
        <Route path="/1" component={SlideOne} />
        <Route path="/2" component={SlideTwo} />
        <Route path="/3" component={SlideThree} />
        <Route path="/4" component={SlideFour} />
        <Route path="/5" component={SlideFive} />
    </BrowserRouter>
)

ReactDOM.render(routing, document.getElementById('root'));
serviceWorker.unregister();
