import React from 'react'
import P5Wrapper from 'react-p5-wrapper';
import sketch from '../sketches/circle'
import prism from 'prismjs'
import '../App.css'
import '../prism.css'
import { SSL_OP_SSLEAY_080_CLIENT_DH_BUG } from 'constants';

class SlideOne extends React.Component{

    constructor(){
        super();
        this.styleCenter = {
            display: 'flex', alignContent: 'center', justifyContent: 'center',
            marginBottom: '50px'
        }

        this.styleSideMargin ={
            marginLeft: '100px',
            marginRight: '100px'
        }

        this.styleParagraph = {
            fontSize: '20px',
            display: 'flex', alignContent: 'center', justifyContent: 'center',
            marginLeft: '100px',
            marginRight: '100px'
        }
        this.styleFooter = {
            fontSize: '15px',
            display: 'flex', alignContent: 'center', justifyContent: 'center'
        }
    }

    componentDidMount() {
        prism.highlightAll();
    }

    render(){
        return(
            <div>
                <h3 style={this.styleCenter}>
                    Rysowanie okręgu
                </h3>
                <p style={this.styleCenter}>
                    Wcisnij spacje aby zresetowac animacje
                </p>

                <div style={this.styleCenter}>
                    <P5Wrapper sketch={sketch}></P5Wrapper>
                </div>

                <p style={this.styleParagraph}>
                    Okrąg można narysować poprzez wyznaczenie 360 punktów, których współrzędne x i y mapujemy odpowiednio
                    do funkcji sinus i cosinus kąta a: (0, 360) pomnożonych przez porządany promień okręgu. Jeżeli chcemy
                    wyznaczyć konkretne współrzędne środka, wystarczy dodać je do każdego z wyznaczonych punktów.
                </p>

                <pre style={this.styleSideMargin}><code class="language-javascript">
                {
                `
                function drawCircle(cx, cy, r){
                    beginShape()
                    for(int a = 0; a < 360; a++){
                        let x = r * sin(a / 57.3) + cx
                        let y = r * cos(a / 57.3) + cy
                        vertex(x, y);
                    }
                    endShape()
                }
                `
                }
                </code></pre>     

                <p style={this.styleParagraph}>
                    W rezultacie dzieki powyzszej funkcji otrzymamy okrąg o promieniu r i środku o współrzędnych
                    cx i cy
                </p><br></br><br></br>

                <p style={this.styleFooter}>
                    Przygotowane w celach edukacyjnych. &reg;Michał Szóstak 2019r
                </p>
           
            </div>    
        );
    }
}

export default SlideOne;