import React from 'react'
import sketch from '../sketches/move_circle'
import P5Wrapper from 'react-p5-wrapper';
import prism from 'prismjs'

class SlideTwo extends React.Component{

    constructor(){
        super();
        this.styleCenter = {
            display: 'flex', alignContent: 'center', justifyContent: 'center',
            marginBottom: '50px'
        }

        this.styleSideMargin ={
            marginLeft: '100px',
            marginRight: '100px'
        }

        this.styleParagraph = {
            fontSize: '20px',
            display: 'flex', alignContent: 'center', justifyContent: 'center',
            marginLeft: '100px',
            marginRight: '100px'
        }
        this.styleFooter = {
            fontSize: '15px',
            display: 'flex', alignContent: 'center', justifyContent: 'center'
        }
    }

    componentDidMount(){
        prism.highlightAll();
    }


    render(){
        return(
            <div>
                <h3 style={this.styleCenter}>
                    Ruch po okregu
                </h3>
                <p style={this.styleCenter}>
                    Uzyj strzalek aby poruszyc czerwonym punktem
                </p>

                <div style={this.styleCenter}>
                    <P5Wrapper sketch={sketch}></P5Wrapper>
                </div>    

                <p style={this.styleParagraph}>
                   Używając wiedzy na temat rysowania okręgu w łatwy sposób możemy przemienić ją w coś bardziej
                   dynamicznego, na przykład zmapować ruch jakiegoś obiektu do wybranego okręgu.
                </p>

                <pre style={this.styleSideMargin}><code class="language-javascript">
                {
                `
                    function draw(){
                      if(keyIsPressed && keyCode == LEFT_ARROW){
                        angle++;
                        x = r * sin(angle / 57.3) + ox;
                        y = r * cos(angle / 57.3) + oy;
                      }
                
                      if(keyIsPressed && keyCode == RIGHT_ARROW){
                        angle--;
                        x = r * sin(angle / 57.3) + ox;
                        y = r * cos(angle / 57.3) + oy;
                      }

                      point(x, y)
                    }
                  
                `
                }
                </code></pre>     

                <p style={this.styleParagraph}>
                    W powyższej funkcji została użyta ta sama technika co w przykładzie pierwszym. Przy pomocy funkcji
                    sinusa i cosinusa odpowiedniego kąta wyznaczamy wspolrzedne x i y. Kąt natomiast zwiększamy lub 
                    zmniejszamy przy pomocy strzałek.
                </p><br></br><br></br>

                <p style={this.styleFooter}>
                    Przygotowane w celach edukacyjnych. &reg;Michał Szóstak 2019r
                </p>         
            </div>    
        );
    }
}

export default SlideTwo;