import React from 'react'
import sketch from '../sketches/rotation'
import P5Wrapper from 'react-p5-wrapper';
import prism from 'prismjs'


class SlideFour extends React.Component{
    constructor(){
        super();
        this.styleCenter = {
            display: 'flex', alignContent: 'center', justifyContent: 'center',
            marginBottom: '50px'
        }

        this.styleSideMargin ={
            marginLeft: '100px',
            marginRight: '100px'
        }

        this.styleParagraph = {
            fontSize: '20px',
            display: 'flex', alignContent: 'center', justifyContent: 'center',
            marginLeft: '100px',
            marginRight: '100px'
        }
        this.styleFooter = {
            fontSize: '15px',
            display: 'flex', alignContent: 'center', justifyContent: 'center'
        }
    }

    componentDidMount(){
        prism.highlightAll();
    }

    render(){
        return(
            <div>
                <h3 style={{display: 'flex', alignContent: 'center', justifyContent: 'center'}}>
                    Rotacja
                </h3>
                <p style={{display: 'flex', alignContent: 'center', justifyContent: 'center'}}>
                    Uzyj myszki aby kontrolowac rotacje trojkata
                </p>
                <div style={{display: 'flex', alignContent: 'center', justifyContent: 'center'}}>
                    <P5Wrapper sketch={sketch}></P5Wrapper>
                </div>

                <p style={this.styleParagraph}>
                   Przydatną funkcją w programowaniu gier/grafiki jest odwócony tangens (atan2). Funkcja
                   ta pozwala na wyliczenie kąta na podstawie przyprostokątnych trójkąta.
                </p>

                <pre style={this.styleSideMargin}><code class="language-javascript">
                {
                `
                function draw(){
                    let angle = atan2(mouseY - ty, mouseX - tx);
                    push();
                    rotate(angle);
                    triangle(a.x, a.y, b.x, b.y, c.x, c.y);
                    pop();
                } 
                `
                }
                </code></pre>     

                <p style={this.styleParagraph}>
                    W powyższym fragmencie kodu widać, że kąt o który trójkąt jest obrócony przypisany jest 
                    do funkcji odwróconego tangensa przyprostokątnych trójkąta który tworzą współrzędne środka
                    trójkąta oraz współrzędne myszki.
                </p><br></br><br></br>

                <p style={this.styleFooter}>
                    Przygotowane w celach edukacyjnych. &reg;Michał Szóstak 2019r
                </p>
            </div>    
        );
    }
}

export default SlideFour;