import React from 'react'
import sketch from '../sketches/rotation'
import P5Wrapper from 'react-p5-wrapper';
import MathJax from 'react-mathjax2';
import prism from 'prismjs'
import '../App.css';



class SlideFive extends React.Component{
    constructor(){
        super();
        this.styleCenter = {
            display: 'flex', alignContent: 'center', justifyContent: 'center',
            marginBottom: '50px'
        }

        this.styleSideMargin ={
            marginLeft: '100px',
            marginRight: '100px',
        }

        this.styleParagraph = {
            fontSize: '20px',
            display: 'flex', alignContent: 'center', justifyContent: 'center',
            marginLeft: '100px',
            marginRight: '100px',
        }

        this.styleFooter = {
            fontSize: '15px',
            display: 'flex', alignContent: 'center', justifyContent: 'center'
        }

        this.formula2D = "{(cos theta, -sin theta), (sin theta, cos theta):}} * {(x), (y):}} = {(x, cos theta, -, y, sin theta), (x, sin theta, +, y, cos theta):}}"
        this.formula3Dx = "{(1, 0, 0), (0, cos theta, -sin theta), (0, sin theta, cos theta):}}";
        this.formula3Dy = "{(cos theta, 0, sin thera), (0, 1, 0), (-sin theta, 0, cos theta):}}";
        this.formula3Dz = "{(cos theta, -sin theta, 0), (sin theta, cos theta, 0), (0, 0, 1):}}";
    }

    componentDidMount(){
        prism.highlightAll();
    }

    render(){
        return(
            <div>
                <h3 style={{display: 'flex', alignContent: 'center', justifyContent: 'center'}}>
                    Macierz rotacji
                </h3>
                <p style={{display: 'flex', alignContent: 'center', justifyContent: 'center'}}>
                    Macierz rotacji wektora [x, y] o kąt theta
                </p>

                <p style={this.styleParagraph}>
                   Poprzedni przykład wykazał że funkcje trygonometryczne mają swoje zastosowanie w rotacji obiektów.
                   Jak wiemy, obiekt geometryczny jest zbiorem punktów w układzie współrzędnych, zatem aby go obrócić
                   musimy wyliczyć nowe współrzędne każdego z kluczowych punktów. Robimy to za pomocą macierzy rotacji.
                </p>   

                <div style={{display: 'flex', alignContent: 'center', justifyContent: 'center'}}>
                    <MathJax.Context input='ascii'>
                        <div>
                            <MathJax.Node>{this.formula2D}</MathJax.Node>
                        </div>
                    </MathJax.Context>
                </div>  

                <p style={this.styleParagraph}>
                    Powyzszy wzór przedstawia rotacje wektora [x, y] o kąt theta w kierunku przeciwnym do kierunku 
                    wskazówek zegara. W kodzie może to wyglądać mniej więcej tak:
                </p>

                <pre style={this.styleSideMargin}><code class="language-javascript">
                {
                `
                function rotate(point, angle){
                    let nPoint = createVector();
                    nPoint.x = (point.x * cos(angle)) - (point.y * sin(angle));
                    nPoint.y = (point.x * sin(angle)) + (point.y * cos(angle));
                    return nPoint;
                } 
                `
                }
                </code></pre>

                <p style={this.styleParagraph}>
                    Nie ogranicza się to do tylko 2 wymiarów. Macierz rotacji stosuje się również w grafice 3D.
                    Wyróżniamy:
                </p>

                <p style={this.styleParagraph}>
                    Rotacja względem osi X:
                </p> 

                <div style={{display: 'flex', alignContent: 'center', justifyContent: 'center'}}>
                    <MathJax.Context input='ascii'>
                        <div>
                            <MathJax.Node>{this.formula3Dx}</MathJax.Node>
                        </div>
                    </MathJax.Context>
                </div>

                <p style={this.styleParagraph}>
                    Rotacja względem osi Y:
                </p>

                <div style={{display: 'flex', alignContent: 'center', justifyContent: 'center'}}>
                    <MathJax.Context input='ascii'>
                        <div>
                            <MathJax.Node>{this.formula3Dx}</MathJax.Node>
                        </div>
                    </MathJax.Context>
                </div> 

                <p style={this.styleParagraph}>
                    Rotacja względem osi Z:
                </p>

                <div style={{display: 'flex', alignContent: 'center', justifyContent: 'center'}}>
                    <MathJax.Context input='ascii'>
                        <div>
                            <MathJax.Node>{this.formula3Dx}</MathJax.Node>
                        </div>
                    </MathJax.Context>
                </div> <br></br><br></br>

                <p style={this.styleFooter}>
                    Przygotowane w celach edukacyjnych. &reg;Michał Szóstak 2019r
                </p>
            </div>    
        );
    }
}

export default SlideFive;