import React from 'react';
import './App.css';
import './components/slide-one.js';
import  {Link} from 'react-router-dom';

class App extends React.Component {

  constructor(){
    super();

    this.slide = 0;
    this.pathToPrev = "/1"
    this.pathToNext = "/" + (this.slide + 1)

    this.next = () => {
      this.slide++;
      this.pathToPrev = "/" + (this.slide == 1 ? 1 : (this.slide - 1));
      this.pathToNext = "/" + (this.slide + 1);
    }

    this.prev = () => {
      this.slide--;
      if(this.slide < 1) this.slide = 1;

      this.pathToPrev = "/" + (this.slide == 1 ? 1 : (this.slide - 1));
      this.pathToNext = "/" + (this.slide + 1);
    }

    this.startPres = () => {
      this.slide = 1;
      this.pathToPrev = "/1";
      this.pathToNext = "/2"
    }
  }

  

  render() {
    return(
      <div>
      <nav>
        <div class="nav-wrapper">
          <Link to="/" class="brand-logo center">Trygonometria - zastosowania</Link>
          <div id="nav-mobile" class="left hide-on-med-and-down">
          <ul style={{display: 'flex', alignContent: 'center', justifyContent: 'center'}}>
            <li style={{display: 'inline-block'}} onClick={this.startPres}>
              <Link to="/1">
                <a class="btn-floating btn-large waves-effect waves-light blue">
                  <i class="material-icons">play_arrow</i>
                </a>
              </Link>
            </li>

            <li style={{display: 'inline-block'}} onClick={this.prev}>
              <Link to={this.pathToPrev}>
                <a class="btn-floating btn-large waves-effect waves-light blue">
                  <i class="material-icons">arrow_back</i>
                </a>
              </Link>
            </li>

            <li style={{display: 'inline-block'}} onClick={this.next}>
              <Link to={this.pathToNext}>
                <a class="btn-floating btn-large waves-effect waves-light blue">
                  <i class="material-icons">arrow_forward</i>
                </a>
              </Link>
            </li>
          </ul>
          </div>
        </div>
      </nav>
      </div>
    )  
  };
}

export default App;
